raul-carousel {
  display: block;
  position: relative;
  font-size: 0.875rem;
}

raul-carousel .r-carousel__container {
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  padding: 0;
  position: relative;
  list-style-type: none;
  width: calc(100% - 36px);
}

raul-carousel .r-carousel__container--full {
  width: 100%;
}

raul-carousel .r-carousel__container--full .r-carousel__slide__container {
  padding: 0;
}

raul-carousel .r-carousel__container--padding {
  padding-bottom: 3rem;
}

raul-carousel .r-carousel--border {
  border-width: 1px;
  border-color: #EBEDEE;
  border-style: solid;
  border-radius: 0.25rem;
}

raul-carousel .r-carousel--padding {
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 2rem;
}

raul-carousel .r-carousel--padding-mobile {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1.5rem;
}

raul-carousel .r-carousel__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}

raul-carousel .r-carousel__slide {
  width: 100%;
  height: 100%;
  position: relative;
}

raul-carousel .r-carousel__slide__container {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  width: 100%;
  display: block;
}

raul-carousel .r-carousel__slide__container__card {
  border-width: 1px;
  border-style: solid;
  border-color: #EBEDEE;
  border-radius: 0.25rem;
  padding: 1.5rem;
  display: block;
}

raul-carousel .r-carousel__pagination__container {
  display: flex;
  position: absolute;
  width: 100%;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
}

raul-carousel .r-carousel__pagination__container--overlap {
  margin-top: -2rem;
  z-index: 10;
}

raul-carousel .r-carousel__pagination__container .r-carousel__button-prev--small, raul-carousel .r-carousel__pagination__container .r-carousel__button-next--small {
  background-color: #fff;
  color: #0076CC;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 1;
  border-radius: 9999px;
  height: 2rem;
  width: 2rem;
  transition: opacity 0.25s;
}

raul-carousel .r-carousel__pagination__container .r-carousel__button-prev--small:hover, raul-carousel .r-carousel__pagination__container .r-carousel__button-next--small:hover {
  border-width: 1px;
  border-style: solid;
  border-color: #EBEDEE;
}

body[modality=keyboard] raul-carousel .r-carousel__pagination__container .r-carousel__button-prev--small:focus, body[modality=keyboard] raul-carousel .r-carousel__pagination__container .r-carousel__button-next--small:focus {
  outline: none;
  border: 1px solid #0076CC;
}

raul-carousel .r-carousel__pagination__container .r-carousel__button--small-disabled {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.25s;
}

raul-carousel .r-carousel__pagination__container .r-carousel__button-prev--small {
  margin-right: 0.5rem;
}

raul-carousel .r-carousel__pagination__container .r-carousel__button-next--small {
  margin-left: 0.5rem;
}

raul-carousel .r-carousel__pagination__bullet {
  background-color: #C6CCD0;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
  position: relative;
  display: inline-block;
  border-radius: 9999px;
  width: 0.5rem;
  height: 0.5rem;
}

body[modality=keyboard] raul-carousel .r-carousel__pagination__bullet:focus {
  outline: none;
}

body[modality=keyboard] raul-carousel .r-carousel__pagination__bullet:focus.r-carousel__pagination__bullet::before {
  position: absolute;
  border-width: 1px;
  border-color: transparent;
  border-radius: 9999px;
  content: "";
  bottom: -4px;
  left: -4px;
  right: -4px;
  top: -4px;
  border: 1px solid #0076CC;
}

raul-carousel .r-carousel__pagination__bullet:hover {
  cursor: pointer;
}

raul-carousel .r-carousel__pagination__bullet--active {
  background-color: #0076CC;
}

raul-carousel .r-carousel__button-next, raul-carousel .r-carousel__button-prev {
  border-width: 1px;
  border-style: solid;
  border-color: #EBEDEE;
  position: absolute;
  background-color: #fff;
  color: #0076CC;
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 50%;
  border-radius: 9999px;
  opacity: 1;
  pointer-events: auto;
  transform: translateY(-50%);
  height: 2rem;
  width: 2rem;
}

body[modality=keyboard] raul-carousel .r-carousel__button-next:focus, body[modality=keyboard] raul-carousel .r-carousel__button-prev:focus {
  outline: none;
  border: 1px solid #0076CC;
}

raul-carousel .r-carousel__button-prev {
  left: 16px;
}

raul-carousel .r-carousel__button-next {
  right: 16px;
}

raul-carousel .r-carousel__button-disabled {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.25s;
}

.r-carousel--navigation-hover > .r-carousel__button-next, .r-carousel--navigation-hover > .r-carousel__button-prev {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.25s;
}

.r-carousel--navigation-hover:hover .r-carousel__button-next, .r-carousel--navigation-hover:hover .r-carousel__button-prev {
  opacity: 1;
  pointer-events: auto;
  transition: opacity 0.25s;
}